@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
}

.App h1{
  font-size: 12rem;
  margin: 0;
}

.App .dates{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  max-width: 100%;
  font-size: 5rem;
}

.App .dates .separator {
  width: 3rem;
}

.App .days-to-end{
  font-size: 2rem;
}

.App .days-to-end.days-2,
.App .days-to-end.days-1{
  color: #CC2222
}

@media (max-width: 980px){
  .App .dates{
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .App .dates {
    flex-direction: column;
  }

  .App h1{
    font-size: 6rem;
  }
  .App .dates{
    font-size: 2rem;
  }
  .App .days-to-end{
    font-size: 1.2rem;
  }

}
html,
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

body{
  background: #f6f6f6;
}

#root{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 20rem;
}



